.container{
    height: 100%;
/* border: 1px solid white; */
overflow-x: hidden;
overflow-y: scroll;
position: relative;
transition: all 0.3s linear;
scroll-behavior: smooth;
}

/* @media screen and (max-width : 738px) {
    .container{
        width: 100%;
    }
} */
#burger{
   background-color: transparent;
   color: white;
   position: fixed;
   top: 20px;
   right: 20px;
   font-size: 28px;
   cursor: pointer;
   display: none;
}

@media screen and (max-width : 738px) {
    #burger{
        display: block;
    }
}